export default "import { Component } from 'solid-js';\nimport { useFormHandler } from 'solid-form-handler';\nimport { yupSchema } from 'solid-form-handler/yup';\nimport { userSchema } from './schema';\nimport {\n  Checkbox,\n  Checkboxes,\n  Radios,\n  Select,\n  TextInput,\n} from '@components/suid';\nimport Button from '@suid/material/Button';\nimport Stack from '@suid/material/Stack';\nimport Grid from '@suid/material/Grid';\nimport Box from '@suid/material/Box';\nimport Typography from '@suid/material/Typography';\n\nexport const Form: Component = () => {\n  const formHandler = useFormHandler(yupSchema(userSchema));\n  const { formData } = formHandler;\n\n  const submit = async (event: Event) => {\n    event.preventDefault();\n    try {\n      await formHandler.validateForm();\n      alert('Data sent with success: ' + JSON.stringify(formData()));\n      formHandler.resetForm();\n    } catch (error) {\n      console.error(error);\n    }\n  };\n\n  const fill = () => {\n    formHandler.fillForm({\n      name: 'John',\n      email: 'john@mail.com',\n      country: 2,\n      favoriteFoods: [3, 4],\n      gender: 'female',\n      subscribed: true,\n    });\n  };\n\n  const reset = () => {\n    formHandler.resetForm();\n  };\n\n  return (\n    <>\n      <form autocomplete=\"off\" onSubmit={submit}>\n        <Box mb={3}>\n          <Typography fontWeight={'bold'}>Using yup schema:</Typography>\n        </Box>\n        <Grid container spacing={2}>\n          <Grid item xs={12}>\n            <TextInput\n              required\n              label=\"Name\"\n              name=\"name\"\n              formHandler={formHandler}\n            />\n          </Grid>\n          <Grid item xs={12}>\n            <TextInput\n              required\n              label=\"Email\"\n              name=\"email\"\n              formHandler={formHandler}\n            />\n          </Grid>\n          <Grid item xs={12}>\n            <Select\n              required\n              label=\"Country\"\n              name=\"country\"\n              placeholder=\"Select a country\"\n              options={[\n                { value: 1, label: 'France' },\n                { value: 2, label: 'Spain' },\n                { value: 3, label: 'Canada' },\n              ]}\n              formHandler={formHandler}\n            />\n          </Grid>\n          <Grid item xs={12}>\n            <Checkboxes\n              required\n              label=\"Favorite foods\"\n              name=\"favoriteFoods\"\n              formHandler={formHandler}\n              options={[\n                { value: 1, label: 'Pizza' },\n                { value: 2, label: 'Hamburger' },\n                { value: 3, label: 'Spaghetti' },\n                { value: 4, label: 'Hot Dog' },\n              ]}\n            />\n          </Grid>\n          <Grid item xs={12}>\n            <Radios\n              required\n              label=\"Gender\"\n              name=\"gender\"\n              formHandler={formHandler}\n              options={[\n                { value: 'male', label: 'Male' },\n                { value: 'female', label: 'Female' },\n                { value: 'other', label: 'Other' },\n              ]}\n            />\n          </Grid>\n          <Grid item xs={12}>\n            <Checkbox\n              label=\"Subscribe to newsletter\"\n              name=\"subscribed\"\n              formHandler={formHandler}\n            />\n          </Grid>\n        </Grid>\n        <Stack spacing={2} direction=\"row\">\n          <Button variant=\"contained\" type=\"submit\">\n            Submit\n          </Button>\n          <Button\n            variant=\"contained\"\n            disabled={formHandler.isFormInvalid()}\n            type=\"submit\"\n          >\n            Submit\n          </Button>\n          <Button variant=\"outlined\" onClick={fill}>\n            Fill\n          </Button>\n          <Button variant=\"outlined\" onClick={reset}>\n            Reset\n          </Button>\n        </Stack>\n      </form>\n      <Box mt={5}>\n        <Typography fontWeight={'bold'}>Form data:</Typography>\n      </Box>\n      <Box\n        mt={3}\n        p={3}\n        component=\"pre\"\n        border={1}\n        borderColor=\"#e4e4e4\"\n        bgcolor=\"#F8F9FA\"\n      >\n        <Box component=\"code\">{JSON.stringify(formData(), null, 2)}</Box>\n      </Box>\n    </>\n  );\n};\n"